import "core-js/modules/es.array.concat";
import request from '@/utils/request';
export function getArt(opt) {
  var catId = opt.catId ? "&cat_id=".concat(opt.catId) : '';
  return request({
    url: "/getArt?curPage=".concat(opt.curPage, "&pageSize=").concat(opt.pageSize).concat(catId)
  });
}
export function getArtDetails(opt) {
  return request({
    url: "/getArtDetails?art_id=".concat(opt.artId)
  });
}
export function getCat() {
  return request({
    url: '/getCat'
  });
}
export function getComment(opt) {
  return request({
    url: "/getComment?art_id=".concat(opt.artId)
  });
}
export function addComment(data) {
  return request({
    url: '/addComment',
    method: 'POST',
    data: data
  });
}