export default {
  name: 'NextPrve',
  props: {
    next: {
      type: Object,
      default: null
    },
    prve: {
      type: Object,
      default: null
    }
  },
  methods: {
    handleLink: function handleLink(artId) {
      var $router = this.$router;
      var opt = {
        query: {
          artId: artId
        }
      };
      $router.push(opt);
      this.$emit('handleLink');
    }
  }
};